export function NavBar() {
  const pathname = usePathname();
  const [open, setOpen] = useState(false);

  return (
    <header className="sticky top-0 z-40 w-full bg-[#44593C] text-white border-b border-black/10">
      <div className="mx-auto flex h-14 max-w-6xl items-center justify-center gap-12 px-4">
        {/* Centered links */}
        {["Home", "Projects"].map((label) => {
          const href = label === "Home" ? "/" : "/projects";
          const active = pathname === href;

          return (
            <Link
              key={href}
              href={href}
              className={cn(
                "text-sm font-medium transition-colors",
                active ? "text-white" : "text-white/80 hover:text-white"
              )}
            >
              {label}
            </Link>
          );
        })}
      </div>
    </header>
  );
}
